// pages / dashboard / cost-structure

// Modules
import React from "react";
import {connect} from 'react-redux';
import Helmet from 'react-helmet';
import Link, {navigateTo} from "gatsby-link";

// Styles
// import styles from '../../../style/preloader.module.css';

// Components
// import ConversationSidebar from '../../../components/dashboard/conversation/ConversationSidebar';
// import ChatMessage from '../../../components/dashboard/conversation/ChatMessage';


class CostStructureIndex extends React.Component {
    componentDidMount() {
        $('.manage-facility-table').DataTable({
            pageLength: 10,
            responsive: true,
            dom: '<"html5buttons"B>lTfgitp',
            buttons: [
                {
                    extend: 'copy'
                },
                {
                    extend: 'csv'
                },
                {
                    extend: 'excel',
                    title: 'ExampleFile'
                },
                {
                    extend: 'pdf',
                    title: 'ExampleFile'
                },

                {
                    extend: 'print',
                    customize: function(win) {
                        $(win.document.body).addClass('white-bg');
                        $(win.document.body).css('font-size', '10px');

                        $(win.document.body).find('table')
                            .addClass('compact')
                            .css('font-size', 'inherit');
                    }
                }
            ]
        });

        $(".select-state").select2({
            placeholder: "Select State",
            allowClear: true,
            width: '100%'
        });

        $(".select-date-type").select2({
            placeholder: "Select Effective Type",
            allowClear: true,
            width: '100%'
        });

        $(".select-date-month").select2({
            placeholder: "Select Month",
            allowClear: true,
            width: '100%'
        });

        $(".select-date-day").select2({
            placeholder: "Select Day",
            allowClear: true,
            width: '100%'
        });
    }

    addNewStructure() {
        $('#modalNewStructure').modal('show');
    }

    editStructure() {
        $('#modalNewStructure').modal('show');
    }

    deleteConfirmStructure() {
        $('#modalDeleteStructure').modal('show');
    }

    render() {
        if (this.props.user.loggedIn) {

            return (
              <span>

                    {/* Start of Helmet */}
                    <Helmet title="Cost Structure" meta={[
                        {
                            name: 'description',
                            content: 'Cost Structure'
                        }, {
                            name: 'keywords',
                            content: 'Cost Structure'
                        }
                    ]}/>
                    {/* End of Helmet */}


                    {/* Start of Heading */}
                    <div className="row wrapper border-bottom white-bg page-heading">
                        <div className="col-lg-8">
                            <h2>Cost Structure</h2>
                            <ol className="breadcrumb">
                                <li>
                                    <Link to="/dashboard">Home</Link>
                                </li>
                                <li className="active">
                                    <strong>Cost Structure</strong>
                                </li>
                            </ol>
                        </div>
                    </div>
                    {/* End of Heading */}



                    {/* Start of Modal */}
                    <div className="modal inmodal" id="modalNewStructure" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog modal-lg">
                            <div className="modal-content animated fadeIn">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-calculator modal-icon"></i>
                                    <h4 className="modal-title">Add New Cost Structure</h4>
                                </div>
                                <div className="modal-body">
                                    <form className="form-horizontal">
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldName">Name *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Cost Structure Name" id="fieldName" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="fieldState" className="col-lg-2 control-label">Type *</label>
                                            <div className="col-lg-10">
                                                <select className="select-state form-control" id="fieldState" defaultValue="" required >
                                                    <option></option>
                                                    <option value="Fixed Cost">Fixed Cost</option>
                                                    <option value="On Per Square Feet Basis">On Per Square Feet Basis</option>
                                                    <option value="One Time Cost">One Time Cost</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="fieldZipcode">Cost *</label>
                                            <div className="col-lg-10">
                                                <input type="text" placeholder="Cost - Accept Figure Only" id="fieldZipcode" className="form-control" defaultValue="" required />
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label className="col-lg-2 control-label" htmlFor="effeciveDate">Effective Date *</label>
                                            <div className="col-lg-4">
                                                <select className="select-date-type form-control" id="effeciveDate1" required >
                                                    <option></option>
                                                    <option value="Once Every Month">Once Every Month On</option>
                                                    <option value="Once Every Year">Once Every Year On</option>
                                                </select>
                                            </div>
                                            <div className="col-lg-3">
                                                <select className="select-date-month form-control" id="effeciveDate2" required >
                                                    <option></option>
                                                    <option value="January">January</option>
                                                    <option value="February">February</option>
                                                    <option value="March">March</option>
                                                    <option value="April">April</option>
                                                    <option value="May">May</option>
                                                    <option value="June">June</option>
                                                    <option value="July">July</option>
                                                    <option value="August">August</option>
                                                    <option value="September">September</option>
                                                    <option value="October">October</option>
                                                    <option value="November">November</option>
                                                    <option value="December">December</option>
                                                </select>
                                            </div>
                                            <div className="col-lg-3">
                                                <select className="select-date-day form-control" id="effeciveDate3" required >
                                                    <option></option>
                                                    <option value="1st">1st</option>
                                                    <option value="2nd">2nd</option>
                                                    <option value="3rd">3rd</option>
                                                    <option value="4th">4th</option>
                                                    <option value="5th">5th</option>
                                                    <option value="6th">6th</option>
                                                    <option value="7th">7th</option>
                                                    <option value="8th">8th</option>
                                                    <option value="9th">9th</option>
                                                    <option value="10th">10th</option>
                                                    <option value="11th">11th</option>
                                                    <option value="12th">12th</option>
                                                    <option value="13th">13th</option>
                                                    <option value="14th">14th</option>
                                                    <option value="15th">15th</option>
                                                    <option value="16th">16th</option>
                                                    <option value="17th">17th</option>
                                                    <option value="18th">18th</option>
                                                    <option value="19th">19th</option>
                                                    <option value="20th">20th</option>
                                                    <option value="21th">21th</option>
                                                    <option value="22th">22th</option>
                                                    <option value="23th">23th</option>
                                                    <option value="24th">24th</option>
                                                    <option value="25th">25th</option>
                                                    <option value="26th">26th</option>
                                                    <option value="27th">27th</option>
                                                    <option value="28th">28th</option>
                                                </select>
                                            </div>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="fieldState" className="col-lg-2 control-label">Applied To *</label>
                                            <div className="col-lg-10">
                                                <select className="select-state form-control" id="fieldState"  required >
                                                    <option></option>
                                                    <option value="Every Residents">Every Residents</option>
                                                    <option value="Larry Ricksaw">Larry Ricksaw</option>
                                                    <option value="Loreal Paris">Loreal Paris</option>
                                                    <option value="Kila Havier">Kila Havier</option>
                                                    <option value="Rocky Walls">Rocky Walls</option>
                                                    <option value="Osmani Escort">Osmani Escort</option>
                                                </select>
                                            </div>
                                        </div>

                                        <small>(*) fields are required to be filled in</small>
                                    </form>
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-primary">Save Changes</button>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className="modal inmodal" id="modalDeleteStructure" tabIndex="-1" role="dialog"  aria-hidden="true">
                        <div className="modal-dialog">
                            <div className="modal-content animated bounce">
                                <div className="modal-header">
                                    <button type="button" className="close" data-dismiss="modal"><span aria-hidden="true">&times;</span><span className="sr-only">Close</span></button>
                                    <i className="fa fa-exclamation-triangle modal-icon"></i>
                                    <h4 className="modal-title">Are you sure?</h4>
                                    This action is irreversible!
                                </div>
                                <div className="modal-footer">
                                    <button type="button" className="btn btn-white" data-dismiss="modal">Close</button>
                                    <button type="button" className="btn btn-danger">Proceed</button>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Modal */}


                    {/* Start of Content */}
                    <div className="wrapper wrapper-content animated fadeInDown">
                        <div className="row">
                            <div className="col-lg-12">
                                <div className="ibox float-e-margins">
                                    <div className="ibox-title">
                                        <h5>Cost Structure Table</h5>
                                        <div className="ibox-tools">
                                            <a className="collapse-link">
                                                    <i className="fa fa-chevron-up"></i>
                                                </a>
                                            <a className="dropdown-toggle" data-toggle="dropdown" href="#">
                                                    <i className="fa fa-wrench"></i>
                                                </a>
                                            <ul className="dropdown-menu dropdown-user">
                                                <li><a href="#">Config option 1</a>
                                                </li>
                                                <li><a href="#">Config option 2</a>
                                                </li>
                                            </ul>
                                            <a className="close-link">
                                                <i className="fa fa-times"></i>
                                            </a>
                                        </div>
                                    </div>
                                    <div className="ibox-content">
                                        <button type="button" className="btn btn-w-m btn-primary pull-right" onClick={this.addNewStructure.bind(this)}>
                                            Add New Cost Structure
                                        </button>
                                        <div className="clearfix space-15"></div>
                                        <div className="table-responsive">
                                            <table className="table table-striped table-bordered table-hover manage-facility-table">
                                                <thead>
                                                    <tr>
                                                        <th>Cost Structure Name</th>
                                                        <th>Type</th>
                                                        <th>Cost</th>
                                                        <th>Effective Date</th>
                                                        <th>Applied To</th>
                                                        <th>Edit Structure</th>
                                                        <th>Delete Structure</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Maintenance Fee
                                                        </td>
                                                        <td>
                                                            On Per Square Feet Basis
                                                        </td>
                                                        <td>
                                                            RM 0.25
                                                        </td>
                                                        <td>
                                                            On the 28th of each month
                                                        </td>
                                                        <td>
                                                            Every Residents
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editStructure.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmStructure.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Sinking Fund
                                                        </td>
                                                        <td>
                                                            Fixed Cost
                                                        </td>
                                                        <td>
                                                            RM 100.00
                                                        </td>
                                                        <td>
                                                            On the 28th of each month
                                                        </td>
                                                        <td>
                                                            Every Residents
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editStructure.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmStructure.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                    <tr className="table-row-vertical-align-middle">
                                                        <td>
                                                            Penalty for damaging lamp post on parking B2
                                                        </td>
                                                        <td>
                                                            Fixed Cost
                                                        </td>
                                                        <td>
                                                            RM 128.00
                                                        </td>
                                                        <td>
                                                            One-time-charge on 23th of this month
                                                        </td>
                                                        <td>
                                                            <a href="#">Larry Ricksaw</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.editStructure.bind(this)}>Edit</a>
                                                        </td>
                                                        <td>
                                                            <a href="#" onClick={this.deleteConfirmStructure.bind(this)}>Delete</a>
                                                        </td>
                                                    </tr>
                                                </tbody>
                                                {/*
                                                <tfoot>
                                                    <tr>
                                                        <th>ID</th>
                                                        <th>Transaction Date</th>
                                                        <th>Bill Type</th>
                                                        <th>Payment Method</th>
                                                        <th>Total Amount (RM)</th>
                                                        <th>Payment Status</th>
                                                        <th>View Payment</th>
                                                    </tr>
                                                </tfoot>
                                                */}
                                            </table>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    {/* End of Content */}

                </span>
            );
        } else {
            return null;
        }
    }
};

const mapStateToProps = (state) => {
    return {user: state.users.user};
}

const mapDispatchToProps = (dispatch) => {
    return {};
}

export default connect(mapStateToProps, mapDispatchToProps())(CostStructureIndex);



